<template>
  <div id="app">
    <v-app id="inspire">
      <v-layout row>
        <v-flex xs12 sm6 offset-sm3>
          <v-card>
            <v-card-title class="blue white--text">
              <span class="headline">LT App</span>
              <v-spacer></v-spacer>

              <v-menu bottom left>
                <template v-slot:activator="{ on }">
                  <v-btn adrk icon v-on="on">
                    <v-icon>more_vert</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-tile v-for="(item, i) in items" :key="i">
                    <v-list-tile-title @click="delete_all">{{ item.title }}</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </v-card-title>

            <v-card-text>
             <router-view/>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-app>
    <v-footer class="pa-3">
      <v-spacer></v-spacer>
      <div>mog&copy; {{ new Date().getFullYear() }}</div>
    </v-footer>
  </div>
</template>

<script>
export default {
  data: () => ({
    items: [
      { title: 'Clear all data'}
    ]
  }),
  methods: {
    delete_all () {
      console.log("delete all")
      this.$db.sensors.clear()
      this.$db.notes.clear()
    }
  },
  created () {
    this.$db.version(1).stores({
      notes: "++id, name",
      sensors: "++id, number, date, accX, accY, accZ"
    })
  }
}
</script>

<style lang="stylus">
@import '~vuetify/src/stylus/main' // Ensure you are using stylus-loader
</style>
